import Component from './Component';
import { capitalize } from '@lib/helper';
import { CLASSES, VIEW_TYPE } from '@lib/constants';

const TAB = CLASSES.FOOTER.TAB;
const CLEAR_BUTTON = CLASSES.FOOTER.CLEAR_BUTTON;

export default class TodoFilter extends Component {
  template() {
    const { viewType, activeCount, completedCount } = this.props;
    const getActiveClass = (type) => (type === viewType ? ` ${CLASSES.FOOTER.TAB_ACTIVE}` : '');
    return `
      <div class="${CLASSES.FOOTER.ITEM_LEFT}">${activeCount} item${
      activeCount > 1 ? 's' : ''
    } left</div>
      <div class="${CLASSES.FOOTER.TABS}">
        ${Object.values(VIEW_TYPE)
          .map(
            (type) =>
              `<div data-status="${type}" class="${TAB}${getActiveClass(type)}">${capitalize(
                type
              )}</div>`
          )
          .join('')}
      </div>
      <div class="${CLEAR_BUTTON}">Clear Completed (${completedCount})</div>
    `;
  }

  setEvent() {
    const { updateViewType, removeCompletedItem } = this.props;

    this.addEvent('click', `.${TAB}`, ({ target }) => {
      updateViewType(target.dataset.status);
    });

    this.addEvent('click', `.${CLEAR_BUTTON}`, () => {
      removeCompletedItem();
    });
  }
}
