import { getElement, getUniqueId } from '@lib/helper';
import { VIEW_TYPE, CLASSES } from '@lib/constants';
import Component from '@components/Component';
import TodoInput from '@components/TodoInput';
import TodoList from '@components/TodoList';
import TodoFilter from '@components/TodoFilter';
import './styles/index.css';

export default class TodoListApp extends Component {
  setup() {
    this.state = {
      items: [],
      viewType: VIEW_TYPE.ALL,
    };
  }

  template() {
    return `
      <div class="${CLASSES.HEADER.PREFIX}"></div>
      <div class="${CLASSES.MAIN.PREFIX}"></div>
      <div class="${CLASSES.FOOTER.PREFIX}"></div>
    `;
  }

  mounted() {
    const inputEl = getElement(`.${CLASSES.HEADER.PREFIX}`);
    const listEl = getElement(`.${CLASSES.MAIN.PREFIX}`);
    const filterEl = getElement(`.${CLASSES.FOOTER.PREFIX}`);
    const { viewType, items } = this.getState();
    const { useDnd } = this.props;

    new TodoInput({
      el: inputEl,
      addItem: this.addItem.bind(this),
    }).render();

    new TodoList({
      el: listEl,
      useDnd,
      items: this.getFilteredItems(items, viewType),
      updateItem: this.updateItem.bind(this),
      insertBeforeItem: this.insertBeforeItem.bind(this),
    }).render();

    new TodoFilter({
      el: filterEl,
      viewType,
      updateViewType: this.updateViewType.bind(this),
      removeCompletedItem: this.removeCompletedItem.bind(this),
      activeCount: this.getActiveItems(items).length,
      completedCount: this.getCompletedItems(items).length,
    }).render();
  }

  getActiveItems(items) {
    return items.filter((item) => !item.completed);
  }

  getCompletedItems(items) {
    return items.filter((item) => item.completed);
  }

  getFilteredItems(items, viewType = VIEW_TYPE.ALL) {
    const activeItems = this.getActiveItems(items);
    const completedItems = this.getCompletedItems(items);
    let filteredItems;

    if (viewType === VIEW_TYPE.ACTIVE) {
      filteredItems = activeItems;
    } else if (viewType === VIEW_TYPE.COMPLETED) {
      filteredItems = completedItems;
    } else {
      filteredItems = items;
    }

    return filteredItems;
  }

  addItem(value) {
    const { items } = this.getState();

    this.setState({
      items: this.getFilteredItems([
        ...items,
        {
          id: getUniqueId(),
          title: value,
          completed: false,
        },
      ]),
    });
  }

  removeCompletedItem() {
    const { items } = this.getState();
    if (this.getCompletedItems(items).length > 0) {
      this.setState({
        items: this.getActiveItems(items),
      });
    }
  }

  updateItem(updateItemId) {
    const { items } = this.getState();
    const itemIndex = items.findIndex((item) => item.id === updateItemId);
    if (itemIndex !== -1) {
      const [item] = items.splice(itemIndex, 1);
      item.completed = !item.completed;

      if (item.completed) {
        items.push(item);
      } else {
        items.unshift(item);
      }

      this.setState({
        items,
      });
    }
  }

  updateViewType(newViewType) {
    this.setState({
      viewType: newViewType,
    });
  }

  insertBeforeItem(srcId, destId) {
    if (srcId === destId) {
      return;
    }

    const { items } = this.getState();
    const srcItemIndex = items.findIndex((item) => item.id === srcId);
    if (srcItemIndex === -1) {
      return;
    }

    const [srcItem] = items.splice(srcItemIndex, 1);
    const destItemIndex = items.findIndex((item) => item.id === destId);
    if (srcItem && destItemIndex !== -1) {
      items.splice(destItemIndex, 0, srcItem);
      this.setState({
        items: this.getFilteredItems(items),
      });
    }
  }
}
