import Component from './Component';
import { CLASSES } from '@lib/constants';

export default class TodoItem extends Component {
  template() {
    const { id, title, completed } = this.props;
    return `
      <li class="${CLASSES.MAIN.TODO_ITEM} ${completed ? CLASSES.MAIN.TODO_ITEM_COMPLETED : ''}">
        <input type="checkbox" class="${CLASSES.MAIN.TODO_ITEM_STATUS}" data-id="${id}" ${
      completed ? 'checked' : ''
    }>
        <label class="${CLASSES.MAIN.TODO_ITEM_TITLE}">${title}</label>
      </li>
    `;
  }
}
