import Component from './Component';
import { getElement } from '@lib/helper';
import { CLASSES, KEY, MESSAGES } from '@lib/constants';

const INPUT = CLASSES.HEADER.INPUT;

export default class TodoInput extends Component {
  template() {
    return `
      <input type="text" class="${INPUT}" placeholder="${MESSAGES.PLACEHOLDER}">
    `;
  }

  mounted() {
    getElement(`.${INPUT}`, this.el).focus();
  }

  setEvent() {
    const { addItem } = this.props;

    this.addEvent('keypress', `.${INPUT}`, (event) => {
      const { key, target } = event;
      const value = target.value.trimStart();
      if (key !== KEY.ENTER || value === '') {
        return;
      }

      addItem(value);
    });
  }
}
