import { isObject, isElement } from './helper';
import { MESSAGES } from './constants';

const listener = new Map();

const getRegisteredEvents = (target) => {
  let registeredEvents = listener.get(target);

  if (!registeredEvents) {
    registeredEvents = listener.set(target, new Map()).get(target);
  }

  return registeredEvents;
};

const getHandlers = (target, eventType) => {
  const registeredEvents = getRegisteredEvents(target);
  let handlers = registeredEvents.get(eventType);

  if (!handlers) {
    handlers = registeredEvents.set(eventType, []).get(eventType);
  }

  return handlers;
};

const setRegisteredEvents = (target, eventType, newHandlers) => {
  const registeredEvents = getRegisteredEvents(target);
  registeredEvents.set(eventType, newHandlers);
};

const on = (target, eventType, handler) => {
  const handlers = getHandlers(target, eventType);
  handlers.push(handler);
};

const emit = (target, eventType, eventData) => {
  const handlers = getHandlers(target, eventType);
  handlers.forEach((handler) => handler(eventData));
};

const off = (target, eventType, handler) => {
  const handlers = getHandlers(target, eventType);
  setRegisteredEvents(
    target,
    eventType,
    handlers.filter((fn) => fn !== handler)
  );
};

const mixin = (target) => {
  if (!isElement(target) && !isObject(target)) {
    throw new Error(MESSAGES.ONLY_APPLICABLE_MIXIN_TO_ELEMENT_OBJECT);
  }

  Object.assign(target, {
    on: (eventType, handler) => on(target, eventType, handler),
    off: (eventType, handler) => off(target, eventType, handler),
    emit: (eventType, handler) => emit(target, eventType, handler),
  });
};

export { on, off, emit, mixin };
