import Component from './Component';
import TodoItem from './TodoItem';
import DragAndDrop from '@lib/dnd';
import { CLASSES } from '@lib/constants';

const TODO_LIST = CLASSES.MAIN.TODO_LIST;
const TODO_ITEM_STATUS = `.${CLASSES.MAIN.TODO_ITEM_STATUS}`;

export default class TodoList extends Component {
  template() {
    const { items } = this.props;
    return `
      <ul class="${TODO_LIST}">
          ${items.map((item) => new TodoItem(item).template()).join('')}
      </ul>
    `;
  }

  mounted() {
    const { useDnd, insertBeforeItem } = this.props;
    if (useDnd) {
      const dnd = new DragAndDrop();
      const dropzone = dnd.dropzone(`.${TODO_LIST}`);

      dnd.draggable(`.${CLASSES.MAIN.TODO_ITEM}`);
      dropzone.on('drop', ({ srcElement, destElement }) => {
        if (!srcElement || !destElement) {
          return;
        }
        const srcItemId = srcElement.querySelector(TODO_ITEM_STATUS).dataset.id;
        const destItemId = destElement.querySelector(TODO_ITEM_STATUS).dataset.id;
        insertBeforeItem(srcItemId, destItemId);
      });
    }
  }

  setEvent() {
    const { updateItem } = this.props;

    this.addEvent('change', TODO_ITEM_STATUS, ({ target }) => {
      updateItem(target.dataset.id);
    });
  }
}
