import { getElements, isFunction } from '@lib/helper';

export default class Component {
  el = null;
  props = {};
  state = {};
  handlersByEvent = new Map();

  constructor(options = {}) {
    const { el, ...props } = options;
    this.el = el;
    this.props = props;
    this.setup();
  }

  setup() {}

  setEvent() {}

  mounted() {}

  template() {
    return '';
  }

  render() {
    if (this.el) {
      this.destroy();

      this.el.innerHTML = this.template();
      this.setEvent();
      this.mounted();
    }
  }

  getState() {
    return this.state;
  }

  setState(setter) {
    let newState;
    if (isFunction(setter)) {
      newState = setter(this.state);
    } else {
      newState = {
        ...this.state,
        ...setter,
      };
    }

    this.state = newState;
    this.render();
  }

  getHandlers(eventType) {
    let handlers = this.handlersByEvent.get(eventType);

    if (!handlers) {
      handlers = this.handlersByEvent.set(eventType, []).get(eventType);
    }

    return handlers;
  }

  addEvent(eventType, selector, callback) {
    if (!this.el) {
      return;
    }

    const handlers = this.getHandlers(eventType);
    const children = [...getElements(selector, this.el)];
    const isTarget = (target) => children.includes(target);
    const handler = (event) => {
      if (!isTarget(event.target)) {
        return;
      }
      callback(event);
    };

    this.el.addEventListener(eventType, handler);
    handlers.push(handler);
  }

  destroy() {
    if (this.el) {
      this.handlersByEvent.forEach((value, key) => {
        value.forEach((handler) => {
          this.el.removeEventListener(key, handler);
        });
      });
      this.handlersByEvent.clear();
    }
  }
}
