import { MESSAGES } from './constants';

const getElement = (target, parent) => {
  let element = null;
  if (isString(target)) {
    element =
      parent && isElement(parent) ? parent.querySelector(target) : document.querySelector(target);
  } else if (isElement(target)) {
    element = target;
  }

  if (isNull(element)) {
    throw new Error(MESSAGES.NOT_EXIST_ELEMENT);
  }

  return element;
};

const getElements = (target, parent) => {
  let element = [];
  if (isString(target)) {
    element =
      parent && isElement(parent)
        ? Array.from(parent.querySelectorAll(target))
        : Array.from(document.querySelectorAll(target));
  } else if (isElement(target)) {
    element = [target];
  }

  return element;
};

const isNull = (v) => v === null;
const isString = (v) => typeof v === 'string';
const isFunction = (v) => typeof v === 'function';
const isElement = (v) => v instanceof Element;
const isObject = (v) => Object.prototype.toString.call(v) === '[object Object]';
const capitalize = (string) => {
  let result;
  if (!isString(string)) {
    result = string;
  } else {
    result = string.charAt(0).toUpperCase() + string.slice(1);
  }
  return result;
};
const getUniqueId = () =>
  window.isSecureContext
    ? window.crypto.randomUUID()
    : Date.now().toString(36) + Math.random().toString(36).substring(2);

export {
  getElement,
  getElements,
  getUniqueId,
  isString,
  isElement,
  isNull,
  isObject,
  isFunction,
  capitalize,
};
